/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import { Helmet } from "react-helmet"
import { LOCALES } from '../i18nProvider/constants';


const en_MetaData = {
  'title': 'IG Tracker | The App to track Instagram Profiles 📱😍✅',
  'description':'🥇 An app to track Instagram Profiles and Download Publications without authentication.',
  'url':'https://dreyes17.github.io/ig_tracker_web/',
  'highLightImage':'https://dreyes17.github.io/ig_tracker_web/static/HighlightDisplay-5f67cec88a90dba7e7268e0aea702373.png',
};

const es_MetaData = {
  'title': 'IG Tracker | La App para monitorizar perfiles de Instagram 📱😍✅',
  'description':'🥇 Una app para monitorizar Perfiles de Instagram y Descargar Publicaciones sin autenticarse',
  'url':'https://dreyes17.github.io/ig_tracker_web/es/',
  'highLightImage':'https://dreyes17.github.io/ig_tracker_web/static/HighlightDisplay-61cdb9f131978d58a9601f09012a2cbd.png',
};


function SEO(props) {
  var metaData;
  if(props.locale === LOCALES.SPANISH) {
    metaData = es_MetaData;
    console.log("es");
  }
  else {
    metaData = en_MetaData;
    console.log("en");
  }

  return (
    <Helmet>
      <title>{metaData['title']}</title>
      <meta name="og:title" content={metaData['title']} />
      <meta name="description" content={metaData['description']} />
      <meta name="og:description" content={metaData['description']} />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={metaData['url']} />
      <meta property="og:site_name" content={metaData['title']} />
      <meta property="og:image" content="https://dreyes17.github.io/ig_tracker_web/static/icon-blank-c444e53bf31faf0290b0e25665425322.jpg"/>
      <meta property="og:image:secure_url" content="https://dreyes17.github.io/ig_tracker_web/static/icon-blank-c444e53bf31faf0290b0e25665425322.jpg"/>
      <meta property="twitter:card" content="summary_large_image"/>
      <meta property="twitter:title" content={metaData['title']}/>
      <meta property="twitter:description" content={metaData['description']}/>
      <meta property="twitter:image" content={metaData['highLightImage']}/>
    </Helmet>
  );
}

export default SEO
